export const getLocale = (): string => {
  const urlParams = new URLSearchParams(window.location.search)
  const langParam = urlParams.get('lang')

  if (langParam) {
    return langParam
  }

  //     
  return 'crh'
}

export const getLocalizedStrings = (): LocaleStrings => {
  const locale = getLocale()

  switch (locale) {
    case 'en':
      return LOCALE_EN
    case 'uk':
      return LOCALE_UK
    case 'crh':
    default:
      return LOCALE_CRH
  }
}
